@use '../../styles/partials/fontcolor' as *;
@use '../../styles/partials/mixins' as *;
@use '../../styles/partials/variables' as *;
@use '../../styles/global.scss' as *;

.mountainsForecast {
	box-sizing: border-box;
	display: flex;
	flex-direction: column;
	width: 850px;

	&-label {
		display: flex;
		flex-direction: row;
		align-items: center;
		padding: 0.5rem 1rem;
		background-color: rgba(95, 158, 160, 0.555);

		&__item {
			&-unit {
				@include unit-size;
			}
			&-title {
				@include forecast-label;
			}
		}
	}

	&-list {
		align-items: center;
		display: flex;
		flex-direction: row;
		width: 100%;
		padding: 1rem;

		&:nth-child(even) {
			background-color: $lightblue;
		}
		&:nth-child(odd) {
			background-color: white;
		}

		&__name {
			text-decoration: none;
			color: $darkblue;
			position: relative;
			overflow: hidden;

			// &::before {
			// 	content: "";
			// 	position: absolute;
			// 	width: 0;
			// 	height: 1.5px;
			// 	top: 50%;
			// 	left: 0;
			// 	background-color: $darkblue;
			// 	transition: width 0.3s ease-in-out;
			// }

			// &:hover {
			// 	&::before {
			// 		width: 60%;
			// 	}
			// }
		}
	}
}

.col {
	&-lg {
		flex: 2;
		font-weight: 700;
		color: rgb(48, 99, 227);
	}
	
	&-sm {
		flex: 1;
		text-align: center;
	}
}